* {
    box-sizing: border-box;
    margin: 0;
  }
  
  html, body {
    margin: 0;
    /* background: white; */
    font-family: 'Montserrat', helvetica, arial, sans-serif;
    font-size: 14px;
    font-weight: 400;
  }
  
  .link {
    display: block;
    text-align: center;
    color: #777;
    text-decoration: none;
    padding: 10px;
  }
  .movie_card {
    position: relative;
    display: block;
    width: 400px;
    height: 200px;
    margin: 20px ;
    /* overflow: hidden; */
    border-radius: 10px;
    transition: all 0.4s;
    box-shadow: 0px 0px 120px -25px rgba(0, 0, 0, 0.5);
  }
  .movie_card:hover {
    transform: scale(1.02);
    box-shadow: 0px 0px 80px -25px rgba(0, 0, 0, 0.5);
